@import url('https://fonts.googleapis.com/css2?family=Mulish&display=swap');

* {
  font-family: 'Mulish', sans-serif;
  box-sizing: border-box;
}

.navbar {
  margin-bottom: 20px;
}

.container {
  margin-bottom: 50px;
}

.navbar-container {
  margin-bottom: 0px;
}

.navbar.container{
  margin-bottom: 20px;
}

.card-title {
  display: block;
  font-weight: 600;
  font-size: medium;
  margin-bottom: 12px;    
  min-height: 20px;
}

.card-subtitle {  
  line-height: 22px;  
}

.btn {
  max-width: 120px;
  margin: 12px 2px;
}

.item-group {
  margin-bottom: 0;
}

.item-group:last-child {
  border: transparent;
}

h2 {
  text-align:center;
}